.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
  height: 100%;
  background-color: rgb(161, 252, 252);
}

.container {
  max-width: 375px;
  padding-left: 18px;
  padding-right: 18px;
  margin-left: auto;
  margin-right: auto;
}
ul {
  list-style: none;
  padding: 0;
}
a {
  text-decoration: none;
}
body {
  margin: 0px;
  font-family: 'Poppins', Sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

